import ServiceItemListView from '../components/ServiceItemListView'
import RegisterView from '../components/Register'
import LoginView from '../components/Login'
import FolksView from '../components/Folks'
import LandingView from '../components/Landing'
import ContactView from '../components/ContactUs'
import { store } from '../store/store-config'

const ifAuth = (to, from, next) => {
  if (store.getters.getAuth) {
    next()
    return
  }
  next('/login')
}

const ifNotAuth = (to, from, next) => {
  if (!store.getters.getAuth) {
    next()
    return
  }
  next('/folks/landing')
}

export default [
  { path: '/', redirect: '/folks' },
  { path: '/folks', component: FolksView, beforeEnter: ifNotAuth },
  { path: '/folks/landing', component: LandingView, beforeEnter: ifAuth },
  { path: '/contactus', component: ContactView, beforeEnter: ifAuth },
  { path: '/:type(medical|household|legal|entertainment|financial)', component: ServiceItemListView, beforeEnter: ifAuth },
  { path: '/register', component: RegisterView },
  { path: '/login', component: LoginView, beforeEnter: ifNotAuth }
]
