<template>
  <v-layout row wrap pt-5 mt-5>
    <service-itemlist
    v-for="(service, index) in $store.getters.displayServices"
    :key="index"
    :service="service"
    />
  </v-layout>
</template>

<script>
import ServiceItemlist from './ServiceItemList'

export default {
  components: {
    ServiceItemlist
  },
  beforeMount () {
    this.$store.dispatch('fetchListData', {
      type: this.type
    })
  },
  computed: {
    type () {
      return this.$route.params.type
    }
  }
}
</script>
