<template>
  <v-layout row wrap class="ma-5 pt-5">
    <v-layout>
      <v-flex xs12 pa-3>
        <h1>Contact Us</h1>
        <blockquote class="bquote">If you have encountered any problems with this website or have any suggestions, please fill out the form below and one of our representatives will contact you directly, to assist you with your requirements.</blockquote>
    </v-flex>
    </v-layout>
    <v-flex xs10 sm8 md5>
      <v-flex pa-3 class="text-xs-center">
        <h3 class="text-xs-left">Send Message</h3>
        <v-divider></v-divider>
        <v-form
          ref="form"
          v-model="valid"
          lazy-validation
        >
          <v-text-field
            v-model="name"
            :rules="nameRules"
            prepend-inner-icon="person"
            label="Name"
            required
          ></v-text-field>

          <v-text-field
            v-model="email"
            :rules="emailRules"
            prepend-inner-icon="email"
            label="E-mail"
            required
          ></v-text-field>

          <v-text-field
            v-model="phone"
            :rules="phoneRules"
            prepend-inner-icon="phone"
            label="Phone Number"
            required
          ></v-text-field>

          <v-select
            v-model="select"
            :items="category"
            :rules="categoryRules"
            label="Category"
            required
          ></v-select>

          <v-textarea
            v-model="message"
            name="input-7-1"
            label="Message"
          ></v-textarea>

          <v-checkbox
            v-model="checkbox"
            label="I am not a robot."
            :rules="checkboxRules"
            required
          ></v-checkbox>

          <v-btn
            color="rgba(200, 200, 200)"
            @click="submitForm"
          >
            Submit
          </v-btn>
        </v-form>
      </v-flex>
    </v-flex>
    <v-flex xs10 sm8 md5 offset-md1>
      <v-flex pa-3>
        <h3>Alternatives</h3>
        <v-divider></v-divider>
        <blockquote class="bquote">In case your form could not reach us for any reason, you can contact us through our phone number and email stated below:</blockquote>
        <v-btn
          flat
          href="tel: 03-612345678"
        >
          <h4>03-612345678</h4>
        </v-btn>
        <v-btn
          flat
          href="mailto: contact@folks.com"
        >
          <h4>contact@folks.com</h4>
        </v-btn>
      </v-flex>
    </v-flex>
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Sending Message...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="success"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="green"
        dark
      >
        <v-card-text>
          <v-icon>done</v-icon>
          Message successfully sent!
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import { setTimeout } from 'timers'
export default {
  data () {
    return {
      valid: true,
      name: '',
      email: '',
      phone: '',
      message: '',
      select: null,
      checkbox: false,
      category: [
        'General Assistance',
        'Technical Assistance',
        'Suggestion',
        'Others'
      ],
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length >= 3) || 'At least 3 characters'
      ],
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(v) || 'E-mail must be valid'
      ],
      phoneRules: [
        v => !!v || 'Phone number is required',
        v => (v && v.length >= 10) || 'At least 10 digits'
      ],
      categoryRules: [
        v => !!v || 'Category is required'
      ],
      checkboxRules: [
        v => !!v || 'Checkbox must be ticked'
      ],
      loading: false,
      success: false
    }
  },
  watch: {
    loading (val) {
      if (!val) return
      setTimeout(() => {
        this.loading = false
        this.success = true
      }, 3000)
    },
    success (val) {
      if (!val) return
      setTimeout(() => (this.success = false), 1500)
    }
  },
  methods: {
    submitForm () {
      if (this.$refs.form.validate()) {
        this.loading = true
      }
    }
  }
}
</script>

<style scoped>
  .bquote {
    font-size: 18px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
</style>
