<template>
    <v-layout>
      <v-flex xs10 sm8 md4 offset-xs1 offset-sm2 offset-md4>
        <v-card class="rounded-card elevation-12">
          <v-flex px-5 py-3 class="text-xs-center">
            <v-card-title class="justify-center">
              <h2>Login</h2>
            </v-card-title>
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
            >
              <v-text-field
                v-model="user.name"
                :rules="nameRules"
                prepend-inner-icon="person"
                label="Name"
                required
              ></v-text-field>

              <v-text-field
                v-model="user.password"
                :rules="passwordRules"
                prepend-inner-icon="lock"
                :append-icon="showPassword ? 'visibility' : 'visibility_off'"
                :type="showPassword ? 'text' : 'password'"
                @click:append="showPassword = !showPassword"
                label="Password"
                required
              ></v-text-field>

              <v-btn
                color="rgba(200, 200, 200)"
                @click="login"
              >
                Login
              </v-btn>

              <v-card-text>
                Don't have an account?
                <v-btn
                  color="blue"
                  flat
                  to="/register"
                >
                  Register Here
                </v-btn>
              </v-card-text>
            </v-form>
          </v-flex>
        </v-card>
      </v-flex>
    </v-layout>
</template>

<script>
export default {
  data () {
    return {
      valid: true,
      showPassword: false,
      user: {
        name: '',
        password: ''
      },
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length >= 3) || 'At least 3 characters'
      ],
      passwordRules: [
        v => !!v || 'Password is required',
        v => (v && v.length >= 8) || 'At least 8 characters'
      ]
    }
  },
  methods: {
    login () {
      if (this.$refs.form.validate()) {
        this.$store.dispatch('fetchUsers')
        if (this.validate()) {
          this.$store.commit('setLoginUser', this.user)
          this.$router.push('/folks/landing')
        }
      }
    },
    validate () {
      let users = this.$store.getters.getUsers
      if (users.length) {
        for (let i = 0; i < users.length; i++) {
          if (users[i].name === this.user.name) {
            if (users[i].password === this.user.password) {
              return true
            }
          }
        }
      }
    }
  }
}
</script>

<style scoped>
  .rounded-card {
    border-radius: 30px;
  }
</style>
